import {ITask} from "core/logic/index"
import {GameLogic} from "core/types/gameLogic"
import {RandomGenerator} from "core/utils/index"
import {Anagramma} from "games/polyglot/common/logic"
import * as _ from "lodash"
import {Level} from "./levels"
import {WordMasterWords} from "./words"

const categoryCount = 2
const wrongWordsCount = 3

export interface Task extends ITask {
  anagrammas: Anagramma[]
  seqPart: boolean
  answerAnagramma: Anagramma
  answers: { seq: string[] }
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator() // [4, 2,8,10]
  index = 0

  createAnagramma(word: string) {
    const words = word.split(" ")
    return {
      anagramma: words.map(x => this.rng.derangementString(x)).join(" "),
      word
    }
  }

  getSubcategories(categories: string[][][]) {
    const category = this.rng.sample(categories)
    const [answer, wrong] = this.rng.subset(category, WordMasterWords.minSubcategoryCount)
    return [answer, wrong]
  }

  getCategories(categories: string[][][]): [string[], string[]] {
    const [answerCategory, wrongCategoriesData] = this.rng.split(categories, 1)
    const wrongCategory = this.rng.sample(wrongCategoriesData)
    return [_.flattenDeep(answerCategory) as any[], _.flattenDeep(wrongCategory) as string[]]
  }

  categoriesByLength = WordMasterWords.categoriesByLength(this.gameData.settings.custom.locale)
  tutorial = WordMasterWords.tutorial(this.gameData.settings.custom.locale)

  createData(level: Level) {
    const categoriesByLength = this.categoriesByLength[level.wordLength]
    const moreThenOneSubcategory = categoriesByLength.filter(x => x.length > 1)
    const categories = level.subcategory ? moreThenOneSubcategory : categoriesByLength

    const [answerCategoryWords, wrongCategoryWords] = level.subcategory
      ? this.getSubcategories(categories)
      : this.getCategories(categories)

    const answerAnagramma = this.createAnagramma(this.rng.sample(answerCategoryWords))
    const wrongAnagrammas = this.rng.take(wrongCategoryWords, wrongWordsCount).map(x => this.createAnagramma(x))
    const anagrammas = this.rng.shuffle(wrongAnagrammas.concat([answerAnagramma]))
    return {
      answerAnagramma,
      anagrammas,
    }
  }

  createTutorial() {
    if (this.index >= WordMasterWords.tutorial.length) {
      return undefined
    }
    const anagrammas = this.tutorial[this.index].map(x => this.createAnagramma(x))
    const answerAnagramma = anagrammas[0]
    this.index += 1
    return {
      answerAnagramma,
      anagrammas,
    }

  }

  nextTask(level: Level) {
    const data = this.isTutorial ? this.createTutorial() : this.createData(level)
    if (!data) {
      return undefined
    }
    const {answerAnagramma, anagrammas} = data
    const wordParts = answerAnagramma.word.split(" ")
    const seqPart = wordParts.length > 1
    const seqCount = seqPart ? Math.ceil(wordParts[0].length / 2) : wordParts[0].length
    const answerIndex = anagrammas.indexOf(answerAnagramma)
    const seq = _.take(answerAnagramma.word.split("").map(x => x + answerIndex.toString()), seqCount)
    const answers = {seq}
    const task = {
      answerAnagramma,
      anagrammas,
      seqPart,
      answers
    }
    return task
  }

}





