import {translationService} from "core/services"
import {Signals} from "core/ui/animations/signals"
import {Sizes} from "core/ui/common/sizes"
import {TutorialTextComponent} from "core/ui/components/games/tutorialTextComponent"
import {screenSizeService} from "core/ui/services/instances"
import {Undef} from "core/utils/undef"
import {BaseWordMainComponent} from "games/polyglot/common/components/baseWordMainComponent"
import {WordListComponent} from "games/polyglot/common/components/wordListComponent"
import {TypeWriterComponent} from "games/polyglot/writer/components/typeWriterComponent"
import {Translations} from "games/polyglot/writer/translations"
import {Task} from '../logic'

const positions = Sizes.create({
  tutorialMinHeight: 290,
})

const positionsPortrait = Sizes.update(positions, {
  tutorialMinHeight: 450,
})

const getPositions = () => screenSizeService.isPortrait ? positionsPortrait : positions

export class MainComponent extends BaseWordMainComponent {
  wordList: WordListComponent

  updateTutorialState(step: number) {
    const states = [
      () => {
        this.tutorial.setProps("")
      },
      () => {
        this.updateTutorial()
      },
      () => {
        this.tutorial.setProps(this.tutorialText2())
      },
      () => {
        this.tutorial.setProps(translationService.getTranslation(Translations.title3))
      },
    ]
    Undef.map(states[step], fn => fn())

  }

  onInit(): void {
    this.useTutorial = true
    this.positions = Sizes.update(this.positions, {
      mainGroup: {y: 130},
      wordlistOffset: {x: 24, y: (this.isTutorial && screenSizeService.isPortrait) ? -80 : 0}
    })

    this.positionsPortrait = Sizes.update(this.positionsPortrait, {
      mainGroup: {y: 280},
    })

    this.typewriter = this.mainGroup.add(this.createComponent(TypeWriterComponent))
      .setPos(this.getPositions().typewriter)
    this.hintComponent.enableLetterHint = true
    this.hintComponent.tooltip = translationService.getTranslation(Translations.hint)
    super.onInit()

    if (this.isTutorial) {
      this.onChangeFor(() => this.tutorialStore.state, state => this.updateTutorialState(state.step))
    }

  }

  createTutorial() {
    return this.add(this.createComponent(TutorialTextComponent)).make({
      minHeight: getPositions().tutorialMinHeight,
      stepCount: 3,
      textAlign: "center"
    })
  }

  updateTutorial() {
    if (this.tutorial) {
      if (this.wordList) {
        this.wordList.setAnswerHightlightDisabled(true)
      }
      this.bringToTop(this.tutorial)
      const anagrammaText = this.getCurrentTask<Task>().anagrammas.map(x => x.anagramma + "  " + x.word).join("\n")
      const lines = [translationService.getTranslation(Translations.title1), anagrammaText]
      this.tutorial.setProps(lines.join("\n"))
    }

  }

  tutorialText2() {
    return translationService.getTranslation(Translations.title2) + ": "
      + this.getCurrentTask<Task>().answerAnagramma.word
  }

  onNewTask(task: Task) {
    this.renderTask([task.answerAnagramma.word], task.anagrammas)

    const signal = this.isFirstTask
      ? this.context.uiService.mainComponent.onIntroComplete
      : Signals.memorize()
    signal.addOnce(() => {
      this.tutorialStore.initTutorial()
    })
  }

}



