import {Locale} from "core/logic/cnst"
import {Strings} from "core/utils/strings"
import {CommonWords} from "games/polyglot/common/commonWords"
import {WordsEn} from "games/polyglot/writer/wordsEn"
import {WordsRu} from "games/polyglot/writer/wordsRu"
import {LocaleUtl} from "games/utils/localeUtl"
import * as _ from "lodash"
import {Dictionary} from "lodash"

export enum WordLength {
  Short, Medium, Long, Longest, Phrase
}

function getWordLength(word: string) {
  const l = word.length
  if (l <= 4) return WordLength.Short
  if (l <= 6) return WordLength.Medium
  if (l <= 8) return WordLength.Long
  return WordLength.Longest
}

export module WordMasterWords {

  const localeData = {
    ru: WordsRu.data,
    en: WordsEn.data
  }


  export const minSubcategoryCount = 2
  export const minWordCount = 3
  export const minWordLength = 3

  function wordLengthAndAnagrammasFilter(locale: Locale, wl: WordLength) {
    const isPhrase = wl == WordLength.Phrase
    const data = LocaleUtl.getData(localeData, locale).words
    const x = _(data).toPairs().map(([k, v]) => _(v).toPairs()
      .filter(([k, v]) => isPhrase ? k == "phrases" : k != "phrases")
      .map(([k, v]) => _.uniq(Strings.splitTrim(v as any).map(x => x.toLowerCase()))
        .filter(x => {
          return (isPhrase || getWordLength(x) == wl) && !CommonWords.anagramms.has(x)
            && (isPhrase || x.indexOf(" ") < 0) && x.length >= minWordLength
        })).filter(v => v.length >= minWordCount)
      .filter(v => v.length >= minSubcategoryCount).value()).filter(v => !_.isEmpty(v)).value()
    return x
  }

  function groupByWordLength(words: string[]): Dictionary<string[]> {
    return _.groupBy(words, 'length')
  }

  export const wordLengthList = [
    WordLength.Short, WordLength.Medium,
    WordLength.Long, WordLength.Longest,
    WordLength.Phrase
  ]
  export const categoriesByLength: (locale: Locale) => Dictionary<string[][][]> = (locale: Locale) =>
    _.fromPairs(wordLengthList.map(x => [x, wordLengthAndAnagrammasFilter(locale, x)]))
  export const tutorial = (locale: Locale) => LocaleUtl.getData(localeData, locale).tutorial

}



