import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {Img} from "core/ui/controls/img"
import {tuple} from "core/utils/tuples"
import {BaseTypeWriterComponent} from "games/polyglot/common/components/baseTypewriterComponent"
import {Images} from "games/polyglot/writer/images"
import {Task} from "games/polyglot/writer/logic"
import {Sounds} from "games/polyglot/writer/sounds"

const positions = Sizes.create({
  partAnimation: -100
})

const durations = {
  typePart: 100
}

export class TypeWriterComponent extends BaseTypeWriterComponent {
  private typePart1: Img

  onInit(): void {
    this.positions = Sizes.update(this.positions, {
      maxWordWidth: 420,
      text: tuple(376, 145)
    })
    this.typePart1 = this.addImage([Images.type_part_1, [0, 0]])
    this.addImage([Images.type_paper, [0, 0]])
    this.addImage([Images.type_part_0, [0, 0]])
    super.onInit()
    this.addHands()
  }

  onAnswer(answer: ILastAnswer): void {

    const task = answer.levelTask.task as Task
    if (answer.status == AnswerStatus.Correct && task.seqPart) {
      this.typeWord(task.answerAnagramma.word)
    } else {
      super.onAnswer(answer)
    }
  }

  animationClean() {
    this.playSound(Sounds.backspace)
    Tweens.start({
      target: this.typePart1,
      to: {x: positions.partAnimation},
      duration: durations.typePart,
      simpleYoyo: true
    })
  }
}



