import {createLevelsFromArray} from "core/types/logic"
import * as _ from "lodash"
import {WordLength, WordMasterWords} from "./words"
import wordLengthList = WordMasterWords.wordLengthList

export interface Level {
  wordLength: WordLength
  subcategory: boolean
}

function level(wordLength: WordLength, subcategory: boolean): Level | undefined {
  if (wordLength == WordLength.Phrase && subcategory)
    return undefined
  return {wordLength, subcategory}
}

export const levels = createLevelsFromArray(_.compact(_.flatMap(wordLengthList, l => [false, true].map(s => level(l, s)))))



